<template>
    <el-form :model="saveForm" label-width="120px" ref="saveForm">
        <div style="padding: 20px">
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <span>设置服务器推送地址</span>
                    <el-button
                        v-if="canSave"
                        style="float: right; padding: 3px 0"
                        type="text"
                        @click.native="saveSubmit"
                        >保存</el-button
                    >
                </div>
                <el-row style="padding-bottom: 20px">
                    <el-col style="padding-top: 9px" :span="4"
                        >状态上报接口</el-col
                    >
                    <el-col :span="10"
                        ><el-input
                            placeholder="请输入内容"
                            v-model="saveForm.statusUrl"
                            clearable
                        >
                        </el-input
                    ></el-col>
                </el-row>
                <el-row>
                    <el-col style="padding-top: 9px" :span="4"
                        >数据上报接口</el-col
                    >
                    <el-col :span="10"
                        ><el-input
                            placeholder="请输入内容"
                            v-model="saveForm.dataUrl"
                            clearable
                        >
                        </el-input
                    ></el-col>
                </el-row>
            </el-card>
        </div>
    </el-form>
</template>
<script>
import { setPushUrl, getPushUrl } from "../../api/api";
import { getButtonList } from "../../promissionRouter";
export default {
    data() {
        return {
            buttonList: [],
            canSave: false,

            saveForm: {
                statusUrl: "",
                dataUrl: "",
                oldStatusUrl: "",
                oldDataUrl: "",
            },
        };
    },
    methods: {
        saveSubmit: function () {
            let para = {
                statusUrl: this.saveForm.statusUrl,
                dataUrl: this.saveForm.dataUrl,
            };

            setPushUrl(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "设置成功",
                        type: "success",
                    });
                } else {
                    this.$message({
                        message: res.msg,
                        type: "error",
                    });
                }
            });
        },
        getUrl: function () {
            getPushUrl().then((res) => {
                if (res.success) {
                    res.data.forEach((ii) => {
                        if (ii.SETTINGID == "S196" && ii.value) {
                            this.saveForm.statusUrl = ii.value;
                            this.saveForm.oldStatusUrl = ii.value;
                        } else if (ii.SETTINGID == "S197" && ii.value) {
                            this.saveForm.dataUrl = ii.value;
                            this.saveForm.oldDataUrl = ii.value;
                        }
                    });
                }
            });
        },
    },
    mounted() {
        this.getUrl();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleSave") {
                this.canSave = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style>
.text {
    font-size: 14px;
}

.item {
    margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}
.clearfix:after {
    clear: both;
}

.box-card {
    width: 100%;
}
</style>


